.productDetails {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;

  height: 100%;
  width: 50%;
}

.productContainer {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 30px;

  background-color: #fff;
  padding: 15px 25px;
  height: 95%;
  width: 900px;
}

.leftProductDetails,
.rightProductDetails {
  height: 100%;
  width: 100%;
}

.leftProductDetails img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.rightProductDetails {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
}

.rightTop {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 30px;

  width: 100%;
}

.rightTopDetails h2 {
  margin-bottom: 15px;
}

.rightBottom {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 15px;

  width: 100%;
}

@media only screen and (max-width: 960px) {
  .productContainer {
    width: 700px;
  }
}

@media only screen and (max-width: 760px) {
  .productContainer {
    width: 480px;
  }
}

@media only screen and (max-width: 500px) {
  .productContainer {
    width: 380px;
  }
}
